.acordion-container{
    width: 95%;
    margin-left: auto;
    margin-right: auto;
    max-width: 1143px;
}

.acordion-item{
    overflow: hidden;
    margin-bottom: 10px;
    width: 100%;
}

.acordion-button{
    padding: 20px;
    background-color: #FF9EF0;
    border: none;
    width: 100%;
    color: white;
    font-size: 19px;
    cursor: pointer;
}

.acordion-button::before{
    display: inline-block;
    content: '';
    height: 10px;
    width: 10px;
    margin-right: 12px;
    border-bottom: 2px solid currentColor;
    border-right: 2px solid currentColor;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

.acordion-button[aria-expanded="true"]::before,
.acordion-button[aria-selected="true"]::before{
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

.acordion-panel{
    border: none;
    background-color: #FF9EF0;
    padding: 15px;
    width: 100%;
}

.acordion-text{
    color: white;
    padding: 10px;
}

